import { useEffect, useState } from "react";
import { AnimatePresence, useViewportScroll } from "framer-motion";
import { getMovie, IGetMovie, IGetMoviesResult } from "../../api/api";
import { Link, PathMatch, useMatch, useNavigate } from "react-router-dom";
import { makeImagePath } from "../../util/utils";
import * as S from "./style";
import { useQuery } from "react-query";
import { useRecoilState, useSetRecoilState } from "recoil";
import { bigMovieAtom } from "../../atom/atoms";
import Modal from "../Modal";

interface IMoviesProps {
  data: IGetMoviesResult;
  title: string;
  content: string;
}

function Slider({ data, title, content }: IMoviesProps) {
  const [index, setIndex] = useState(0);
  const [leaving, setLeaving] = useState(false);
  const [back, setBack] = useState(false);

  //   row    row   gap 
  //          leaving
  //    AnimatePresence onExitComplete={toggleLeaving} 
  const toggleLeaving = () => setLeaving((prev) => !prev);

  const increaseIndex = () => {
    if (leaving) return;
    setLeaving(true);
    setBack(false);
    const totalMovies = data?.results.length;
    const maxIndex = Math.floor(totalMovies / offset);
    setIndex((prev) => (prev === maxIndex - 1 ? 0 : prev + 1));
  };

  const decreaseIndex = () => {
    if (leaving) return;
    setLeaving(true);
    setBack(true);
    const totalMovies = data?.results.length;
    const maxIndex = Math.floor(totalMovies / offset);
    setIndex((prev) => (prev === 0 ? maxIndex : prev - 1));
  };

  //    
  // useHistory() :  URL     ( router  )
  const navigate = useNavigate();
  const homeMovieMathch: PathMatch<string> | null =
    useMatch("/movies/:movieId");

  const homeTvMatch: PathMatch<string> | null = useMatch("/tv/:contentId");

  const setBigMovie = useSetRecoilState(bigMovieAtom);

  const onClicked = (contentId: number) => {
    content === "movie"
      ? navigate(`/movies/${contentId}`)
      : navigate(`/tv/${contentId}`);
    setBigMovie([contentId, content]);
  };

  //             
  const onOverlayClick = () =>
    content === "movie" ? navigate("/") : navigate("/tv");

  //           
  const { scrollY } = useViewportScroll();

  const clikedMovie = data?.results.find((movie: any) =>
    content === "movie"
      ? movie.id + "" === homeMovieMathch?.params.movieId
      : movie.id + "" === homeTvMatch?.params.contentId
  );

  const [offset, setOffset] = useState(5);
  const changeOffset = () => {
    if (500 <= window.innerWidth && window.innerWidth <= 799) {
      setOffset(3);
    } else if (800 <= window.innerWidth && window.innerWidth <= 1099) {
      setOffset(4);
    } else if (1100 <= window.innerWidth && window.innerWidth <= 1399) {
      setOffset(5);
    } else if (1400 <= window.innerWidth) {
      setOffset(6);
    }
  };
  useEffect(() => {
    window.addEventListener("resize", changeOffset);
    return () => {
      window.removeEventListener("resize", changeOffset);
    };
  }, []);

  return (
    <>
      <S.Sliders>
        <S.SliderTitle>
          <Link to="/">
            <div>{title}</div>
          </Link>
          <S.ArrowBox>
            <S.Arrow
              onClick={decreaseIndex}
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 320 512"
            >
              <path d="M224 480c-8.188 0-16.38-3.125-22.62-9.375l-192-192c-12.5-12.5-12.5-32.75 0-45.25l192-192c12.5-12.5 32.75-12.5 45.25 0s12.5 32.75 0 45.25L77.25 256l169.4 169.4c12.5 12.5 12.5 32.75 0 45.25C240.4 476.9 232.2 480 224 480z" />
            </S.Arrow>
            <S.Arrow
              onClick={increaseIndex}
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 320 512"
            >
              <path d="M96 480c-8.188 0-16.38-3.125-22.62-9.375c-12.5-12.5-12.5-32.75 0-45.25L242.8 256L73.38 86.63c-12.5-12.5-12.5-32.75 0-45.25s32.75-12.5 45.25 0l192 192c12.5 12.5 12.5 32.75 0 45.25l-192 192C112.4 476.9 104.2 480 96 480z" />{" "}
            </S.Arrow>
          </S.ArrowBox>
        </S.SliderTitle>
        <S.Slider>
          <AnimatePresence
            initial={false}
            onExitComplete={toggleLeaving}
            custom={back}
          >
            <S.Row
              custom={back}
              key={index}
              variants={S.rowVariants}
              initial="hidden"
              animate="visible"
              exit="exit"
              transition={{ type: "tween", duration: 1 }}
            >
              {data.results
                .slice(offset * index, offset * index + offset)
                .map((item) => (
                  <S.RowItem
                    layoutId={item.id + ""}
                    key={item.id}
                    variants={S.boxVariants}
                    initial="normal"
                    whileHover="hover"
                    transition={{ type: "tween" }}
                    onClick={() => onClicked(item.id)}
                  >
                    <S.Box bgphoto={makeImagePath(item.backdrop_path)}>
                      <S.BoxInner>
                        <S.Img
                          variants={S.infoVariants}
                          bgphoto={makeImagePath(item.backdrop_path)}
                        />
                        <S.Info variants={S.infoVariants}>
                          <S.Title>{item.title || item.name}</S.Title>
                        </S.Info>
                      </S.BoxInner>
                    </S.Box>
                  </S.RowItem>
                ))}
            </S.Row>
          </AnimatePresence>
        </S.Slider>
      </S.Sliders>
      <AnimatePresence>
        {clikedMovie && (
          <>
            <S.Overlay
              onClick={onOverlayClick}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
            ></S.Overlay>
            <S.BigMovie
              layoutId={clikedMovie.id + ""}
              style={{
                top: scrollY.get() - 200,
              }}
            >
              {clikedMovie && (
                <>
                  <S.BigCover
                    style={{
                      backgroundImage: `linear-gradient(to top, #181818, transparent), url(${makeImagePath(
                        clikedMovie.backdrop_path
                      )})`,
                    }}
                  />
                  <S.BigInfo>
                    <S.BigTitle>
                      {clikedMovie.title || clikedMovie.name}
                    </S.BigTitle>
                    <S.BigDesc>
                      <S.BigOverview>
                        <S.BigOverviewText>
                          {clikedMovie.overview}
                        </S.BigOverviewText>
                      </S.BigOverview>

                      <Modal />
                    </S.BigDesc>
                  </S.BigInfo>
                </>
              )}
            </S.BigMovie>
          </>
        )}
      </AnimatePresence>
    </>
  );
}
export default Slider;
